.badge{--size: 25px;width:var(--size);height:var(--size);border-radius:calc(var(--size)/2);background:gray;color:#fff;display:inline-flex;align-items:center;justify-content:center;vertical-align:middle;box-shadow:0 2px 4px var(--shadow-color);cursor:pointer;transition:.2s;font-size:calc(.6*var(--size));margin-right:7px;border:1px solid #fff}.badge:hover{transform:scale(1.2, 1.2)}.badge:last-of-type{margin-right:0px}.badge .BadgeIcon{display:flex;justify-content:center;align-items:center;vertical-align:middle}